export var Form = function(formElement, loader, validator)
{
    this.formElement = formElement;
    this.loader = loader;
    this.validator = validator;

    this.attachToSubmitButton();
    this.attachToCancelButton();
}


Form.prototype = Object.create(EventEmitter.prototype);

Form.prototype.attachToSubmitButton = function()
{
    var self = this;

    this.formElement.find("#submitButton").bind("click", function(e)
    {
        var values = self.loader.pullValues();
        var errors = self.validator.validate(values);

        if (_.keys(errors).length > 0)
            self.onInvalidForm(values, errors);
        else
            self.onSubmit(values);
    })
}

Form.prototype.attachToCancelButton = function()
{
    var self = this;

    this.formElement.find("#cancelButton").bind("click", function(e)
    {
        self.onCancel();
    })
}

Form.prototype.getRecord = function()
{
    return this.record;
}

Form.prototype.loadRecord = function(record)
{
    this.record = record || new Object();
    this.loader.loadForm(this.record);
}

Form.prototype.updateRecord = function(record, values)
{
    _.extend(record, values);
}

Form.prototype.onSubmit = function(values)
{
    var self = this;

    if (this.record)
    {
        this.updateRecord(this.record,values);
        this.emit("submit", [this.record]);
    }
    else
    {
        this.emit("submit", [values]);
    }
}

Form.prototype.onCancel = function()
{
    this.emit("cancel", [this.record]);
}

Form.prototype.onInvalidForm = function(values, errors)
{
    this.emit("invalid", [this.record, values, errors]);
};export var FormBuilder = function(config)
{
    this.fields = config.fields;
    this.buttons = this.createButtonConfig(config.buttons || new Object());
}

FormBuilder.prototype = {};

FormBuilder.prototype.buildForm = function(formSelector, cb)
{
    var self = this;
    var formDiv = $(formSelector);
    var formLoader;
    var formValidator;

    formDiv.addClass("form-all");

    async.parallel(
        [

            function(done)
            {
                async.forEachSeries(

                    self.fields,

                    function(field, next)
                    {
                        self.addField(formDiv, field, next);
                    },

                    function()
                    {
                        self.addButtons(formDiv);

                        done();
                    }
                );
            },

            function(done)
            {
                self.buildLoader(formDiv, function(loader)
                {
                    formLoader = loader;
                    done();
                });
            },

            function(done)
            {
                self.buildValidator(formDiv, function(validator)
                {
                    formValidator = validator;
                    done();
                });
            }
        ],

        function()
        {
            var form = new Form(formDiv, formLoader, formValidator);
            cb(null, form);
        }
    );
}

FormBuilder.prototype.addField = function(form, fieldDef, cb)
{
    if (fieldDef.type == "hidden")
    {
        var input = this.buildInput(fieldDef, form);
        form.append(input);
        return cb();
    }
    else
    {
        if (fieldDef.type == "subForm")
        {
            // subform widget needs access to form
            fieldDef = _.extend(
            {
                form: form
            }, fieldDef);
        }

        return this.buildField(fieldDef, form, function(err, field)
        {
            form.append(field);
            cb();
        });
    }
}

FormBuilder.prototype.addButtons = function(form)
{
    var buttons = this.buildButtons();

    form.append(buttons);
}

FormBuilder.prototype.buildField = function(fieldDef, cb)
{
    var field = $("<div></div>");
    field.addClass("form-line");

    this.buildInput(fieldDef, function(err, input)
    {
        var label = this.buildLabel(fieldDef);
        var subLabel = this.buildSubLabel(fieldDef);

        if (label)
            field.append(label);

        field.append(input);

        if (subLabel)
            field.append(subLabel);

        cb(null, field);
    });
},

FormBuilder.prototype.buildLabel = function(fieldDef)
{
    if (!fieldDef.label)
        return undefined;

    var label = $("<label></label>");
    label.addClass("form-label-left");
    label.attr("for", fieldDef.id);
    label.text(fieldDef.label);

    if (fieldDef.isRequired)
        label.append("<span class='form-required'>*</span>");

    return label;
},

FormBuilder.prototype.buildSubLabel = function(fieldDef)
{
    if (!fieldDef.subLabel)
        return undefined;

    var subLabel = $("<label></label>");
    subLabel.addClass("form-sub-label");
    subLabel.attr("for", fieldDef.id);
    subLabel.html(fieldDef.subLabel);

    return subLabel;
},

FormBuilder.prototype.buildInput = function(fieldDef, form, cb)
{
    var input = $("<div></div>");
    var control;

    input.addClass("form-input");

    switch (fieldDef.type)
    {
        case "checkbox":
            input = $("<span></span>").appendTo(input);
            input.addClass("form-checkbox-item");

            control = $("<input type='checkbox' />");
            control.addClass("form-checkbox");
            control.attr("value", "true");
            break;

        case "date":
            control = $("<input type='date' />");
            control.addClass("form-textbox");
            break;

        case "datetime":
            control = $("<input type='text' />");
            control.addClass("form-textbox");
            control.attr("size", "25");
            break;

        case "display":
            control = $("<span></span>");
            break;

        case "file":
            control = $("<input type='file' />");
            control.addClass("form-textbox");
            control.attr("size", "20");
            break;

        case "hidden":
            control = $("<input type='hidden' />");
            break;

        case "number":
            control = $("<input type='number' />");
            control.addClass("form-textbox");
            control.attr("size", "5");
            control.attr("min", "0");
            break;

        case "select":
            control = $("<select></select>");
            control.addClass("form-dropdown");
            control.css("width", "150px");
            break;

        case "subForm":
            control = $("<a href='javascript: void(0);'>" + fieldDef.linkText + "</a>");
            control.attr("linkText", fieldDef.linkText);
            control.subform(subformConfig);
            break;

        case "text":
            control = $("<input type='text' />");
            control.addClass("form-textbox");
            control.attr("size", "25");
            break;

        case "textarea":
            control = $("<textarea></textarea>");
            control.addClass("form-textarea");
            control.attr("cols", "30");
            control.attr("rows", "6");
            break;

        default:
            throw new Error("[Form Builder] buildInput() error - unexpected field type: " + fieldDef.type);
    }

    control.attr("id", fieldDef.id);
    input.append(control);

    if (fieldDef.inputDesc)
        input.append("<br />" + fieldDef.inputDesc);

    if (fieldDef.type == "select")
    {
        this.addSelectOptions(fieldDef, control, function()
        {
            cb(null, input);
        })
    }
    else
    {
        cb(null, input);
    }
}

FormBuilder.prototype.addSelectOptions = function(fieldDef, select, cb)
{
    this.getSelectOptions(fieldDef, function(err, options)
    {
        if (fieldDef.isRequired === undefined || fieldDef.isRequired == false)
        {
            select.append("<option></option>");
        }

        options.forEach(function(option)
        {
            var value = option["value"];
            var label = option["label"];

            select.append("<option value='" + value + "'>" + label + "</option>");
        });

        cb();
    })
}

FormBuilder.prototype.getSelectOptions = function(fieldDef, cb)
{
    if (fieldDef.options)
    {
        cb(null, fieldDef.options);
    }
    else if (fieldDef.optionsUrl)
    {
        $.get(fieldDef.optionsUrl, function(data)
        {
            cb(null, data);
        })
    }
}

FormBuilder.prototype.buildButtons = function()
{
    var self = this;

    var buttons = $("<div></div>");
    buttons.addClass("form-line");

    var label = $("<label></label>");
    label.addClass("form-label-left");
    buttons.append(label);

    if (this.buttons.submit.isVisible())
    {
        var submitButton = $("<button type='submit'></button>");
        submitButton.attr("id", "submitButton");
        submitButton.addClass("form-submit-button");
        submitButton.text(this.buttons.submit.getLabel());
        buttons.append(submitButton);
    }

    if (this.buttons.cancel.isVisible())
    {
        var cancelButton = $("<button type='submit'></button>");
        cancelButton.attr("id", "cancelButton");
        cancelButton.addClass("form-submit-button");
        cancelButton.text(this.buttons.cancel.getLabel());
        buttons.append(cancelButton);
    }

    return buttons;
}

FormBuilder.prototype.createButtonConfig = function(config)
{
    return {
        submit: this.Button(_.defaults(config.submit || new Object(),
        {
            label: "Submit",
            visible: true
        })),
        cancel: this.Button(_.defaults(config.cancel || new Object(),
        {
            label: "Cancel",
            visible: true
        }))
    };
}

FormBuilder.prototype.Button = function(config)
{
    return {
        isVisible: function()
        {
            return config.visible;
        },
        getLabel: function()
        {
            return config.label;
        }
    };
}

FormBuilder.prototype.buildFormLoader = function(formDivSelector, cb)
{
    var formLoader = new FormLoader(this.fields, formDivSelector);

    cb(formLoader);
}

FormBuilder.prototype.buildFormValidator = function(formDivSelector, cb)
{
    var formValidator = new FormValidator(this.fields, formDivSelector);

    cb(formValidator);
}